import axios from 'axios'
import {baseUrl, _reject, _rejectObj} from './config'
import Cookies from 'js-cookie';

/**
 * banner
ULR:http://domain/eryuzhisen-server/getHomeBanner
Method:GET
response:
body json:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1,
	"errinfo":"xxxx",
	"list":[
		{
		"title":"xxxx",//
		"image_url":"http://xxxx",//
		"jumpui":{
			"page_id":"xx",//id,
			"arguments":{"":""},//,
			}
		},
	....
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getHomeBanner(option){
	var url = baseUrl + 'getHomeBanner';
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
            switch(Number(response.data.errcode)){
                default:
                    msg = '';
                    break;
            }
            return _reject({
            	ret: response.data.ret,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

