/**
 * 
http://domain/eryuzhisen-server/opus/getCatalogList
Method:POST //,post

request:
params:
"page":"1",//,
"pageSize":"10",// 10

body://
{
	"user_id":"xx",//
	"category_id":"11",//
	"fuzzy_query":"xxxx",//
	"catalog_status":"1",//  0  1 
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getCatalogList(option){
	var url = baseUrl + 'opus/getCatalogList'; 
	var prosime = axios({
		method: 'POST',
		url: url,
		params: option.params,
		data: JSON.stringify(option.body)
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
            switch(Number(response.data.errcode)){
                default:
                    msg = '';
                    break;
            }
            return _reject({
            	ret: response.data.ret,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * 
URL:http://domain/eryuzhisen-server/user/getUserList
Method:GET

request:
params:
"query":"xxxx"//
"page":"1",//,
"pageSize":"10",// 10

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"more":"0",// 0  1 ,
	"list":[
		{
			"uid":"xxxx",
			"nick_name":"xxxxx",//
			"avatar_url":"xxxxx",//url
			"signature":"xxxxx",//
			"c_age":"xxx",//
			"relation":"0"// 0  1  2  3 

		},
	....
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getUserList(option){
	var url = baseUrl + 'user/getUserList'; 
	var token = Cookies.get('token');

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
            switch(Number(response.data.errcode)){
                default:
                    msg = '';
                    break;
            }
            return _reject({
            	ret: response.data.ret,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * OSSpolicy
URL:http://domain/eryuzhisen-server/upload/getPolicyAndAccess
Method:GET
request:
params:
"type":"1"//  1  2 

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"accessid":"xxxx",//accessidaccessid, .
	"policy":"xxxx",//policybase64
	"signature":"xxxxx",//policy
	"dir":"xxx",//
	"host":"xxxx",//.
	"expire":"xxxx"//
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getPolicyAndAccess(option){
	var url = baseUrl + 'upload/getPolicyAndAccess'; 
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option
	}).then(function (response) {
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

export {
	getHomeBanner,
	getLabelList,
	getCategoryList,
	getCatalogList,
	getPolicyAndAccess,
	getUserList,
	_reject
}





