/**
 * 
 * 
 * baseUrl: 
 * imgBaseUrl: 
 */
var baseUrl; 
var imgBaseUrl = '';
var distUrl = '';

if (process.env.NODE_ENV == 'development') {
	baseUrl = '/java/eryuzhisen-server/';
	distUrl = '/dist/html/';
	imgBaseUrl = '';
}else{
	baseUrl = '/java/eryuzhisen-server/';
	distUrl = '/';
	imgBaseUrl = '';
}

function _reject (option){
	return Promise.reject(option);
}

function _rejectObj(error){
	if (!!error.ret && error.ret > 0) {
		return _reject(error);
	}
    return _reject({
    	ret: error.ret || -1,
		msg: error.msg || '',
		error: error
    })
}

export {
	baseUrl,
	imgBaseUrl,
	distUrl,
	_reject,
	_rejectObj
}


