import {baseUrl, _reject, _rejectObj} from '../config'
import Cookies from 'js-cookie';
// headers
import axios from 'axios'
axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.get['X-Requested-With'] = 'XMLHttpRequest';

/**
 * 
URL:http://domain/eryuzhisen-server/auth/getPicVerifyCode
Method:GET
response:
body json:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"pic_vid":"xxxxx",//id
	"pic_vcode":"xxxx"// base64
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getPicVerifyCode(option){
	var url = baseUrl + 'auth/getPicVerifyCode';
	var prosime = axios({
		method: 'GET',
		url: url,
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
            switch(Number(response.data.errcode)){
                case 10001:
                    msg = '';
                    break;
                default:
                    msg = '';
                    break;
            }
            return _reject({
            	ret: response.data.ret,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

