/**
 * http://domain/eryuzhisen-server/auth/sigin
Method:POST
request:
body json:
{
	"phone_num":"xxxxx",//
	"password":"xxxx",//
	"device_no":"xxx",//,,
	"pic_vid":"xxxx",//5,
	"pic_vcode":"xxxx",//55,
}

response:
body json:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"uid":"xxxx"//id,
	"token":"xxxx",//7 header
	"device_no":"xxxx",//,request,
	"info_status":"1"// 1: ,
	
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function sigin(option){
	var url = baseUrl + 'auth/sigin';
	var _header = option._header || {};
	delete option._header;
	return axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
			switch(Number(response.data.errcode)){
	            case 20011:
	                msg = '';
	                break;
	            case 20001:
	                msg = '';
	                break;
	            case 20005:
	            	if (response.data.pwd_err_times >= 5) {
	            		msg = '!';
	            	} else {
	            		response.data.pwd_err_times = 5;
	            		msg = '!';
	            	}
	            	break;
	            case 20003:
	            	response.data.pwd_err_times = 5;
	            	msg = '!';
	            	break;
	            default:
	                msg = '';
	                break;
	        }
	        return _reject({
            	ret: response.data.ret,
            	pwd_err_times: response.data.pwd_err_times || 0,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});
}

