/**
 * 
URL:http://domain/eryuzhisen-server/user/updateUserInfo
Method:POST
request body
{
	"nick_name":"xxxx",
	"avatar_url":"http://xxxxx.png",
	"signature":"xxxxxxx"
}

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function updateUserInfo(option){
	var url = baseUrl + 'user/updateUserInfo';
	var _header = option._header || {};
	var token = Cookies.get('token');

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
			var msg;
            switch(Number(response.data.errcode)){
                default:
                    msg = '';
                    break;
            }
            return _reject({
            	ret: response.data.ret,
				msg: msg
            })
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

